
import XButton from 'vux/src/components/x-button/index.vue'

import Lib from 'assets/js/Lib'
let page
let location
let searchNo
export default {
components: {
XButton
},
data () {
return {
srcImg: ''
}
},
created () {
page = this
location = window.location.search
searchNo = location.slice(location.indexOf('searchNo'))
},
mounted () {
/* 判断pc和店*/
const flag = Lib.U.IsPC()
const bodyC = document.getElementsByTagName('body')[0]
const location = window.location.hostname
if (flag) {
if (location === '193.0.1.245') {
bodyC.className = 'pcBg anXian'
} else {
bodyC.className = 'pcBg sanJiang'
}
} else {
if (location === '193.0.1.245') {
bodyC.className = 'anXian'
} else {
bodyC.className = 'sanJiang'
}
}
/* 返回到想到的位置*/
pushHistory()
window.addEventListener('popstate', function (e) {
window.location = '/e-Invoice/details.html?' + searchNo
}, false)
function pushHistory () {
var state = {
title: 'title',
url: '#'
}
window.history.pushState(state, 'title', '#')
}

page.srcImg = 'http://193.0.10.163:20002/invoice/pic?' + searchNo
},
methods: {
clickEmail () {
window.location.href = '/e-Invoice/E-mail.html?' + searchNo
}
}
}




